.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(33, 39, 43, 0.8);
  z-index: 999;
}
.modal_close {
  border: 0;
  background: 0;
  width: 36px;
  height: 36px;
  position: absolute;
  padding: 0;
  right: -36px;
  top: -36px;
  opacity: .6;
  transition: all 300ms ease-out 0s;
}
.modal_close:before, .modal_close:after {
  content: '';
  display: block;
  position: absolute;
  width: 30px;
  height: 4px;
  background: white;
  top: 16px;
  left: 2px;
}
.modal_close:before {
  transform: rotate(-45deg);
}
.modal_close:after {
  transform: rotate(45deg);
}
.modal_close:hover {
  opacity: 1;
}
.modal_content {
  position: absolute;
  background: white;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.8);
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  padding: 50px;
  width: 700px;
  box-sizing: border-box;
}
.modal_title {
  font-size: 28px;
  text-align: center;
  color: #262626;
  font-weight: 700;
  margin-bottom: 20px;
}
.modal_subtitle {
  font-size: 16px;
  line-height: 26px;
  color: #575757;
  text-align: center;
  margin-bottom: 30px;
}

